export const translate = (value: string, language: Language) => {
  // FR (default)
  if (language === DEFAULT_LANGUAGE) {
    return value;
  }

  // EN
  const translation = DICTIONARY_EN[value];
  if (!translation) {
    return value;
  }

  return translation;
};
